#overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #cccccc;
    opacity: 0.4;
}

#loading-img {
    position: relative;
    left: -50%;
}

#loading-img-container {
    position: absolute;
    left: 50%;
    top: 50%;
}


